nav{
  height: 60px;
  width: 100%;
  background-color: darkgreen;
  margin: 0;
  ul{
    height: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    margin: 0;
    li{
      list-style: none;
      margin: 0 20px 0 20px;
      display: flex;
      justify-content: center;
      align-items: center;
      cursor: pointer;
      a{
        height: 100%;
        text-decoration: none;
        color: rgba(171, 255, 70, 0.87);
        &:hover {
          color: white;
          transition: all 0.2s ease-in-out;
          text-shadow: 1px 1px 2px black;
        }
      }
    }
  }
}
